body {
    background: rgb(27, 165, 193);
    background: linear-gradient(125deg, rgba(27, 165, 193, 0) 0%, rgba(52, 207, 93, 1) 100%);
    height: 100%;
}

/* header section */
.hp-logo {
    width: 2.1rem;
    height: 2.1rem;
    margin-right: 5px;
}

.head-logo {
    width: 6rem;
    height: 2.5rem;
    margin-left: 5px;
}

.header-para {
    font-size: 1.2rem;
    font-weight: 100;
}

hr {
    display: block;
    height: 2px;
    border: 0;
    border-top: 3px solid #1f1e1e;
    margin: 1em 0;
    padding: 0;
}

/* .drupa-container */

.drupa-container h1 {
    font-family: raleway;
}

.drupa-logo {
    width: 8rem;
    height: 8rem;
    margin: auto;
}

.button-radius {
    border-radius: 0 3rem 3rem 0;
    position: relative;
    font-weight: 700;
    padding: 5px 30px 5px 10px;
    font-size: 0.875rem;
    border: none;
    bottom: -15px;
    left: 50%;
    width: 80%;
    max-width: 250px;
    transform: translateX(-50%);
}

.click-icon {
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 3px;
    position: absolute;
    bottom: -2px;
    right: 1px;
}

/* main image section */
.leftimg,
.rightimg {
    width: 11rem;
    height: 16rem;
    object-fit: cover;
    object-position: 50% 90%;
    top: 2.5rem;
    border: 0.3rem solid white;
}

.leftimg {
    left: -3%;
}

.rightimg {
    right: -3%;
}

.centerimg {
    width: 14rem;
    height: 21rem;
    object-fit: cover;
    object-position: 60% 100%;
    border: 0.3rem solid white;
    z-index: 1;
}

/* printer section */
.main-container {
    margin-top: 15rem;
    color: white;
    padding: 8rem 0 6rem 0;
}

#printer-img {
    width: 100%;
}

.hp-header {
    font-size: 0.9rem;
    font-weight: 700;
}

.print-price {
    font-size: 3.25rem;
}

.printer-about {
    font-size: 0.8rem;
    font-weight: 600;
    line-height: 15px;

}

.digital {
    font-size: 0.5rem;
}

.vertical-line {
    width: 2px;
    margin-top: 40px;
    background-color: rgb(190, 183, 183)
}

.hr-line {
    height: 2px;
    background-color: rgb(190, 183, 183)
}

.hr-line-modile {
    height: 2px;
    margin: 30px auto 20px 20px;
    background-color: rgb(190, 183, 183)
}

.container-content {
    max-width: 992px;
}

/* info-sec */

.info-section {
    background-color: white;
}

.info-para {
    font-size: 1rem;
}

.bg-gradien {
    background-image: linear-gradient(to right, #d2e5d5, #d6ede9, #e4f2f6, #f4f8fc, #ffffff);
    font-size: 1rem;
}

/* footer-section */
.footer-logo {
    width: 90px;
}

.footer-section {
    background-color: rgb(214, 228, 196);
    border-top: 3px solid rgb(9, 98, 9);
}

.footer-30-logo {
    width: 90px;
    height: 70px;
}

.footer-gradient {
    height: 20px;
    background-image: linear-gradient(to left, #03460d, #1f7b26, #2c9043, #4ddd67, #99eca0);
}

.ul-footer {
    font-size: 0.9rem;
}

.footer-multi-gradient {
    width: 20%;
    height: 15px;
}

/* mobile display  */
@media only screen and (min-width: 560px) {

    /* header selection */
    .hp-logo {
        width: 2.5rem;
        height: 2.5rem;
        margin-right: 5px;
    }

    .head-logo {
        width: 7.5rem;
        height: 3rem;
        margin-left: 5px;
    }

    /* .drupa-container */

    .drupa-logo {
        width: 8.1rem;
        height: 8.1rem;
        margin: 0;
    }

    .button-radius {
        /* font-size: 1rem; */
        padding: 0 30px 3px 10px;
        bottom: -1rem;
        left: 24px;
        transform: unset;
    }

    /* printer-section */
    .hp-header {
        font-size: 1.5rem;
        font-weight: 700;
    }

    .digital {
        font-size: 1rem;
    }

    .print-price {
        font-size: 6.5rem;

    }

    .printer-about {
        font-size: 1.25rem;
        font-weight: 500;
        line-height: 28px;
    }

    /* .info-section */
    .info-para {
        font-size: 1.5rem;
    }

    .bg-gradien {
        font-size: 1.5rem;
    }

    /* footer-section */
    .footer-logo {
        width: 110px;
    }

    .footer-section {
        background-color: rgb(214, 228, 196);
        border-top: 3px solid rgb(9, 98, 9);
    }

    .footer-30-logo {
        width: 130px;
        height: 90px;
    }
}

/* tab display */

@media only screen and (min-width: 768px) {

    /* headder section */
    .header-para {
        font-size: 1.6rem;
    }

    /* main images section  */
    .leftimg,
    .rightimg {
        width: 12rem;
        height: 18rem;
        top: 2.5rem;
        border: 0.5rem solid white;
    }

    .leftimg {
        left: 10%;
    }

    .rightimg {
        right: 10%;
    }

    .centerimg {
        width: 13rem;
        height: 22rem;
        border: 0.5rem solid white;

    }

    /* .drupa-container */
    .button-radius {
        left: 48px;
    }

    .main-container {
        margin-top: 13rem;
    }

    .footer-section ul li {
        font-size: 1.3rem;
    }

}

/* mini laptop display */
@media only screen and (min-width: 992px) {

    /* headersection */
    .hp-logo {
        width: 3.9rem;
        height: 3.5rem;
        margin-right: 5px;
    }

    .head-logo {
        width: 9.8rem;
        height: 5rem;
        margin-left: 5px;
    }

    /* .drupa-container   */

    .drupa-container {
        padding: 0 3rem;

    }

    .drupa-container h1 {
        font-size: 2.7rem;
        letter-spacing: 0.3rem;
    }

    .leftimg,
    .rightimg {
        width: 13rem;
        height: 20rem;
        top: 2.5rem;
    }

    .leftimg {
        left: 18%;
    }

    .rightimg {
        right: 18%;
    }

    .centerimg {
        width: 15rem;
        height: 25rem;

    }

    .main-container {
        margin-top: 18rem;
    }
}

/* laptop and system display */
@media only screen and (min-width: 1200px) {
    .container-content.container-expand {
        max-width: 1140px;
    }

    .leftimg,
    .rightimg {
        width: 14rem;
        height: 22rem;
        top: 2.5rem;
        border: 0.5rem solid white;
    }

    .leftimg {
        left: 20%;
    }

    .rightimg {
        right: 21%;
    }

    .centerimg {
        width: 16rem;
        height: 28rem;
        border: 0.5rem solid white;

    }

    .main-container {
        margin-top: 20rem;
    }

    .footer-section ul li {
        font-size: 1.4rem;
    }
}

/* big screen dispaly */
@media screen and (min-width: 1400px) {
    .container-content.container-expand {
        max-width: 1320px;
    }

    .leftimg,
    .rightimg {
        width: 15rem;
        height: 22rem;
        top: 2.5rem;
        border: 0.5rem solid white;
    }

    .leftimg {
        left: 21%;
    }

    .rightimg {
        right: 21%;
    }

    .centerimg {
        width: 17rem;
        height: 27rem;
        border: 0.5rem solid white;

    }

    .footer-logo {
        width: 110px;
    }

    .footer-30-logo {
        width: 120px;
        height: 80px;
    }

}